<template><section><h1>Model</h1>
<p><code v-pre>vc-primitive-model</code> 组件用于加载基于 glTF 的三维模型。</p>
<h2>示例</h2>
<h3>加载 glTF 模型</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-model :url="url" @readyPromise="readyPromise" :modelMatrix="modelMatrix" :scale="10000" :minimumPixelSize="128" :maximumScale="200000" @click="clicked">
        </vc-primitive-model>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-model</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"model"</span>
        <span class="hljs-attr">@readyPromise</span>=<span class="hljs-string">"readyPromise"</span>
        <span class="hljs-attr">:url</span>=<span class="hljs-string">"url"</span>
        <span class="hljs-attr">:modelMatrix</span>=<span class="hljs-string">"modelMatrix"</span>
        <span class="hljs-attr">:scale</span>=<span class="hljs-string">"10000"</span>
        <span class="hljs-attr">:minimumPixelSize</span>=<span class="hljs-string">"128"</span>
        <span class="hljs-attr">:maximumScale</span>=<span class="hljs-string">"200000"</span>
      &gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-model</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">url</span>: <span class="hljs-string">'./statics/SampleData/models/CesiumAir/Cesium_Air.gltf'</span>,
        <span class="hljs-attr">modelMatrix</span>: {}
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.viewer = viewer
        <span class="hljs-keyword">this</span>.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(<span class="hljs-number">105</span>, <span class="hljs-number">38</span>, <span class="hljs-number">10000</span>))
      },
      readyPromise(model) {
        <span class="hljs-keyword">const</span> boundingSphere = Cesium.BoundingSphere.transform(model.boundingSphere, model.modelMatrix)
        <span class="hljs-keyword">this</span>.viewer.scene.camera.flyToBoundingSphere(boundingSphere)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code> 指定 gltf 文件的 url 地址。</td>
</tr>
<tr>
<td>basePath</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 指定 glTF JSON 中的路径相对于的基本路径。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 model 图元是否显示。</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 4x4 转换矩阵，用于将模型从模型坐标转换为世界坐标。</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 model 缩放比例。</td>
</tr>
<tr>
<td>minimumPixelSize</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 model 的最小像素。</td>
</tr>
<tr>
<td>maximumScale</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 model 最大像素。</td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre>optional</code> 指定与 model 关联的信息。</td>
</tr>
<tr>
<td>allowPicking</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定与 model 是否可以被拾取。</td>
</tr>
<tr>
<td>incrementallyLoadTextures</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定在加载模型后纹理是否可以继续流入。</td>
</tr>
<tr>
<td>asynchronous</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 确定在加载所有 glTF 文件后，是否将模型 WebGL 资源创建分散在几个帧或块上，直到完成。</td>
</tr>
<tr>
<td>clampAnimations</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定动画在没有帧动画的时候保持最后一个姿势。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 指定 model 是否投射或接收每个光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 可选的仅用于调试。 为模型中的每个DrawCommand绘制边界球。</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 可选的仅用于调试。 仅用于调试。 在线框中绘制模型。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 model 的高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>scene</td>
<td>Object</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 对于使用height reference属性的模型必须传递。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 model 随相机改变的显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre>'WHITE'</code></td>
<td><code v-pre>optional</code> 指定 model 渲染混合的颜色。</td>
</tr>
<tr>
<td>colorBlendMode</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 model 与颜色混合模式。 <strong>HIGHLIGHT: 0, REPLACE: 1, MIX: 2</strong></td>
</tr>
<tr>
<td>colorBlendAmount</td>
<td>Number</td>
<td><code v-pre>0.5</code></td>
<td><code v-pre>optional</code> 指定 colorBlendMode 为 MIX 的颜色强度。0 表示模型颜色，1 表示纯色，0-1 表示混合。</td>
</tr>
<tr>
<td>silhouetteColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'RED'</code></td>
<td><code v-pre>optional</code> 指定 model 轮廓线颜色。</td>
</tr>
<tr>
<td>silhouetteSize</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 model 轮廓线像素尺寸。</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 model 屏幕裁剪参数。</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 确定是否在GPU上对Draco编码的模型进行了反量化。 这减少了编码模型的总内存使用量。</td>
</tr>
<tr>
<td>credit</td>
<td>Object|String</td>
<td></td>
<td><code v-pre>optional</code> 指定 model 的描述信息。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Model.html">Model</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>model</td>
<td>模型可用时触发。 返回模型对象。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该图元时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          url: './statics/SampleData/models/CesiumAir/Cesium_Air.gltf',
          modelMatrix: {}
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.viewer = viewer
          this.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(105, 38, 10000))
        },
        readyPromise(model) {
          const boundingSphere = Cesium.BoundingSphere.transform(model.boundingSphere, model.modelMatrix)
          this.viewer.scene.camera.flyToBoundingSphere(boundingSphere)
        },
        clicked (e) {
          console.log(e)
        }
      }
    }
  </script>